import React from 'react';
import {
  Switch,
  Link,
  Route,
} from "react-router-dom";
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab } from '@fortawesome/free-brands-svg-icons';
import { faCloudDownloadAlt, faRoute, faSpaceShuttle } from '@fortawesome/free-solid-svg-icons';
import styles from './App.module.css';

import Tutorials from './Tutorials';
import StyleGuide from './StyleGuide';

library.add(fab, faCloudDownloadAlt, faRoute, faSpaceShuttle);

class App extends React.Component {

  homePath = "/";

  tutorialProjectsPath = "/tutorial-projects";

  styleGuidePath = "/style-guide";

  render() {
    return (
      <div className={styles.entirePage}>
        <Header
          homePath={this.homePath}
          tutorialProjectsPath={this.tutorialProjectsPath}
          styleGuidePath={this.styleGuidePath}
        />
        <Switch>
          <Route
            exact
            path={this.homePath}
            component={Home}
          />
          <Route
            path={this.tutorialProjectsPath}
            render={() => <Tutorials pathPrefix={this.tutorialProjectsPath} />}
          />
          <Route
            path={this.styleGuidePath}
            render={() => <StyleGuide pathPrefix={this.styleGuidePath} />}
          />
        </Switch>
      </div>
    );
  }
}

class Header extends React.Component {

  render() {

    const { homePath } = this.props;
    const { tutorialProjectsPath } = this.props;
    const { styleGuidePath } = this.props;


    return (
      <nav className={styles.header}>
        <Link
          className={styles.link}
          to={homePath}
        >
          Home
        </Link>
        <Link
          className={styles.link}
          to={tutorialProjectsPath}
        >
          Tutorial Projects
        </Link>
        <Link
          className={styles.link}
          to={styleGuidePath}
        >
          Style Guide
        </Link>
      </nav>
    );
  }
}

function Home() {
  return (
    <div className="Home">
      <h1>Home</h1>
    </div>
  );
}

export default App;