function Note(props) {
  const { text } = props;
  const { primary } = props;

  const className = primary ? styles.notePrimary : styles.noteSecondary;

  return (
    <div className={className}>
      <strong>NOTE</strong>
      <p>{text}</p>
    </div>
  );
}

function Button(props) {
  const { dark } = props;
  const { children } = props;

  let className;
  if (dark === true) {
    className = styles.buttonDark;
  } else {
    className = styles.buttonLight;
  }

  return (
    <button
      className={className}
      type="button"
    >
      {children}
    </button>
  );
}